<template>


<transition name="fade">
<Modal @closeModal="모달창열렸니=false"
 :원룸들 ="원룸들" :누른거 ="누른거" 
 :모달창열렸니="모달창열렸니"/>
</transition>

<div class="menu">
  <a v-for="menu in 상단메뉴" :key="menu">{{menu}}</a>
 
</div>

<!--컴포넌트 가져와 쓰는곳 -->
<Discount v-if="showDiscount ==true"/>
<p>지금 결제하면 {{amount}}% 할인</p>

<button @click="priceSort">가격순 정렬</button>
<button @click="pricehighSort">가격역순 정렬</button>
<button @click="sortABC">문자열 정렬</button>
<button @click="sortBack">되돌리기</button>


 <Card @openModal="모달창열렸니=true; 누른거 =$event"  :원룸들="원룸들[i]" v-for="(a,i) in 원룸들" :key="i"/>
 
 
 

 <!-- <Card :원룸들="원룸들[1]"/>
 <Card :원룸들="원룸들[2]"/>
 <Card :원룸들="원룸들[3]"/>
