 <Card :원룸들="원룸들[5]"/> -->





  
</template>

<script>
import data from './assets/oneroom.js';
import Discount from './Discount.vue';
import Modal from './Modal.vue';
import Card from './Card.vue';




export default {
  name: 'App',
  data() {
    return {
      amount : 30,
      showDiscount : true,
      원룸들오리지널 : [...data],
      누른거 : 0,
      원룸들 : data,
      모달창열렸니 : false,
      신고수 : [0,0,0],
      상단메뉴 : ['Home' , 'Shop' , 'About'],
      products : ['역삼동원룸' ,'천호동원룸' , '마포구원룸']
      
    }
  },
  //함수
  methods: {
    increase(){
      this.신고수[0] ++;
    },
    priceSort() {
      this.원룸들.sort(function(a,b){
        return a.price- b.price
      })
    },
    pricehighSort() {
      this.원룸들.sort(function(a,b){
        return b.price -a.price
      })
    },
    sortBack() {
      this.원룸들 = [...this.원룸들오리지널];
    },
    sortABC(){
      this.원룸들.sort(function(a,b){
        return a.title.localeCompare(b.title)
      });
    },
 
  
  },
  /*라이프사이클 훅 */
  /*마운트되고나서*/
  // mounted() {
  //   setTimeout(()=>{
  //   this.showDiscount = false
  //   },2000);
  // },

  mounted() {
    setInterval(()=>{
      this.amount--;
    },1000)
  },
  

  components: {
    Discount : Discount,
    Modal : Modal,
    Card : Card,
  }
}



</script>

